body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{display:flex;justify-content:center;align-items:center;transform:translateY(-24px)}.center{width:180px;height:48px;position:absolute}.btn{width:180px;height:48px;cursor:pointer;background:transparent;border:2px solid #91C9FF;border-width:2px;outline:none;transition:1s ease-in-out;display:flex;justify-content:center;align-items:center}svg{position:absolute;left:0;top:0;fill:none;stroke:#fff;stroke-dasharray:150 480;stroke-dashoffset:150;transition:1s ease-in-out}svg.yellowStroke{stroke:#d59638;stroke-width:2px;stroke-dasharray:150 480;stroke-dashoffset:150}.btn:hover{transition:1s ease-in-out}.btn:hover svg{stroke-dashoffset:-480}.btn span{color:white;display:block}

